.p1{
    padding: 4em;
    overflow: hidden;
}
h3 {
    background-color: rgb(229, 228, 228);
    padding: 0.2em;
    border-radius: 50em;
    align-items: center;
    font-size: medium;
    margin: 2em 0 2em 0;
}
h2{
    font-size: 2em;
    text-align: center;
    margin: 1em 0 1em 0;
}
h5{
    margin-top: 0em;
    font-size: 2em;
}
.top-container img{
    width: 100%;
}
.top-container{
    width: 100%;
    margin-top: -3em;
}
.heading {
    margin-top: 3em;
    width: 50%;
    text-align: left;
}
.heading h2{
    text-align: left;
}
#brief {
    text-align: left;
}
.tag{
    display: inline-block;
}
.research img{
    width: 100%;
}
.m-state{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 6em;
    text-align: left;
}
.m-state h3{
    text-align: center;
}
.r1{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    color: white;
    background-color: rgb(45, 46, 47);
    border-radius: 0.8em;
    padding: 6em;
    gap: 6em;
}
.r1-l{
    box-sizing: border-box;
    border-radius: 0.8em;
}
.r1-r{
    text-align: left;
}
.r1 h3{
    text-align: center;
    color: rgb(45, 46, 47);
    background-color: rgba(229, 228, 228, 0.7);
    font-size: medium;
}
.r-user-container{
    padding: 4em 0 2em 0;
}
.r2{
    display: grid;
    grid-template-columns: 2fr 3fr;
    border-radius: 0.8em;
}
.r2-l{
    text-align: left;
    padding: 4em;
    
}
.r2-r{
    text-align: left;
    padding: 4em;
    background-color: rgb(229, 228, 228);
    border-radius: 0.8em;
}
.r2-l h3{
    text-align: center;
    font-size: medium;
}
.r2-r h3{
    text-align: center;
    font-size: medium;
    color: white;  
    background-color: rgba(45, 46, 47,0.5);
}
#chart1{
    width: 110%;
}
#chart2{
    width: 150%;
}
#chart3{
    width: 90%;
}
#chart4{
    width: 60%;
}
#chart5 {
    scale: 90%;
}
#chart6{
    scale: 80%;
}
.UI-container{
    /* background-color: rgb(45, 46, 47); */
    text-align: center;
    margin: auto;
    padding: 4em;
    border-radius: 0.8em;
    /* color: white; */
    width: 80%;
    height: 40%;
}
.UI-container p{
    font-size: 0.9em;
}

.hue-eval {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 0.5em;
    vertical-align: middle;
    text-align: left;
    font-size: small;
}
.hue-eval p{
    background-color: rgba(229, 228, 228, 0.3);
    border-radius: 0.8em;
    padding: 1em;
}
.hue-eval h4{
    background-color: rgb(229, 228, 228);
    border-radius: 0.8em;
    padding: 1em;
    text-align: center;
    color: rgb(45, 46, 47);
}
#hue3{
    background-color: rgb(32, 81, 156);
    color: white;
    border-radius: 0.8em;
    padding: 1em; 
}
#hue2{
    background-color: rgb(62, 115, 196);
    color: white;
    border-radius: 0.8em;
    padding: 1em;
}
.evaluation:nth-child(5){
    grid-column: 5/-2;
}
.evaluation3:nth-child(4){
    grid-column: 4/-1;
    text-align: center;
}
.evaluation9:nth-child(2){
    grid-column: 2/4;
    text-align: center;
}
.evaluation10:nth-child(6){
    grid-column: 6/-1;
    text-align: center;
}
.banking_flow{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 6em;
}
.flow-r a{
    margin-top: 1em;
    background-color: rgb(62, 115, 196);
    color: white;
    border-radius: 0.8em;
    padding: 0.8em;
    display: inline-block;
}
.title-small{
    margin: 4em 4em 4em 4em;
}
.linkforfood{
    margin-top: 2em;
}
.heading a{
    background-color: rgb(221, 66, 10);
    color: white;
    padding: 1em;
    border-radius: 1em;
    font-weight: bold;
}
.ref{
    text-align: right;
    margin: 4em 0 0 0;
}
@media screen and (max-width: 600px){
    .opener h1{
        font-size: 2em;
        margin: 1em 0 2em 0;
    }
    .top-container img{
        margin: auto;
        text-align: left;
        width: 140%;
        object-fit: cover;
        clip-path: inset(0 3em 0 3em);
        transform: translateX(-12.5%);
        border-radius: inherit;
    }
    .heading{
        margin-top: 0em;
        width: 100%;
    }
    .m-state{
        grid-template-columns: repeat(1,1fr);
        gap: 0em;
    }
    .r2{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        border-radius: 0.8em;
    }
    .r2-l{
        text-align: left;
        padding: 0em;
        
    }
    .r2-r{
        text-align: left;
        background-color: rgb(229, 228, 228);
        border-radius: 0.8em;
        padding: 4em 4em 4em 4em;
        margin: 0 -4em 0 -4em;
    }
    .UI-container{
        display: none;
    }
    .banking_flow{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 2em;
        margin-bottom: 4em;
    }
    #next{
        margin-top: 4em;
        background-color: rgb(229, 228, 228);
        border-radius: 0.8em;
        padding: 4em 4em 4em 4em;
        margin: 0 -4em 0 -4em;
    }
}
